.place {
  &__photo-container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    border-radius: 8px;
    transition-property: box-shadow, transform;
    transition-duration: 0.5s;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
    &:hover {
      transform: scale(1.1);
      box-shadow: 0 5px 5px 5px $colorBlue;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    color: $colorBlue;
    text-decoration: none;
    font-weight: bold;
  }
}
